              <h4>Contexte</h4>
              <p>L’application web a été initialement réalisée entre 2018 et 2019 puis re-développée en 2021.</p><br/>
              <p>Pour valider le titre professionnel de concepteur et développeur d’applications que j’ai effectué pendant l’année scolaire 2018-2019, j’ai eu l’opportunité de concevoir et développer un projet « chef-d’oeuvre », permettant de mettre en pratique les compétences développées tout au long de l’année, en réalisant une API REST, de la conception du design jusqu’au déploiement.<p/><br/>
              <p>Ma terre d’accueil n’ayant pas de projet correspondant à mes compétences, j’ai dû réaliser un projet personnel.<br/>
              Étant particulièrement intéressé par le domaine de la psychologie j’ai décidé de réaliser une application web pour aider les personnes en dépression.</p><br/><br/><br/>
              <br>



              <h4>Objectif de l'application</h4>
              <p>
              Aider et accompagner les personnes isolées traversant un Épisode Dépressif Caractérisé (EDC), couramment appelé "dépression",
              en leur proposant un moyen de panser leur blessures émotionnelle par l'organisant de leur pensées.</p>
        
