 <!---------------------------- DEBUT DU DESIGN ------------------------->
      <section class="projet design">

        
        <div class="design__box design__intro">
          <h2 class="projet">LE DESIGN</h2>
            <p>Pour réaliser le design de l’application, la méthodologie de design UX UI, en 4 phases et 5 étapes, a été suivie. <br/><br/> Il s’agit d’une méthodologie centrée utilisateur, composée d’outils et de méthodologies basée sur la notion d’itération entre l’équipe projet et des situations d’usages réels permettant le développement d’un produit de qualité en rapport avec les attentes des utilisateurs.</p>
