 <!--DEBUT DESIGN STRATEGIE---------------------------------------->
          <div class="design__container design__strategie">
          <h3 class="projet">STRATÉGIE</h3>

          <div class="design__strategie--container design__strategie--existant">
              <h4 class="projet mini_top">Existant</h4>
              <p>En 2018, le groupe Baromètre santé 2017 a présenté <strong><a href="http://beh.santepubliquefrance.fr/beh/2018/32-33/2018_32-33_1.html" target="_blank" >une étude </a></strong> sur la dépression en France chez les 18-75 ans. </p>
              <p>L’objectif de ce travail était de présenter les données de prévalence de l’épisode dépressif caractérisé (EDC) en France métropolitaine, en 2017, d’identifier les segments de population les plus concernés et d’observer les évolutions depuis 2005. </p>



              <a class="bouton apro__bouton" a href="http://beh.santepubliquefrance.fr/beh/2018/32-33/2018_32-33_1.html" target="_blank" >DÉCOUVRIR L'ARTICLE</a>    


              <p>Résultats : Près d’une personne sur dix avait vécu un EDC au cours des 12 derniers mois.</p>
              <p>Les femmes, notamment celles âgées de 35-44 ans, les personnes de moins de 45 ans, les chômeurs et autres inactifs, les personnes veuves ou divorcées et les personnes déclarant de faibles revenus présentaient les niveaux de prévalence les plus élevés.</p>
              <p>Après une stabilité observée entre 2005 et 2010, la prévalence de l’EDC a augmenté de 1,8 points sur la période 2010-2017 ; cette augmentation concerne davantage les femmes (+3 points), les 35-44 ans (+4 points), les chômeurs (+5 points) et les individus déclarant des revenus faibles (+3 points).</p>

              <p>La prévalence élevée de la dépression ainsi que son augmentation au cours des dernières années plaident pour un renforcement des dispositifs de prévention et de prise en charge des troubles dépressifs, en particulier à destination des populations les plus vulnérables. 
              Déplus, la dépression est dans notre société, un sujet tabou et toutes les personnes atteintes par cette maladie n’osent pas recourir à l’aide d’un spécialiste.</p>

              <p>C’est pourquoi j’ai décidé de développer cette application regroupant les fonctionnalités nécessaires pour aider les individus sujets à cette maladie, n'etant pas prêts à recourir à une aide extérieure.</p>
          </div>




          <div class="design__strategie--container design__strategie--problematique">
            <h4 class="projet">Problématique</h4>
              <p>"Comment aider, un nombre de plus en plus grandissant de personnes n’étant pas prêtes à recevoir une aide extérieure à surmonter leur EDC."</p>
          </div>

           <div class="design__strategie--container design__strategie--solution">
            <h4 class="projet">Solution</h4>
              <p>D'après l'étude, la dépression se caractérise par une tristesse ou une perte d’intérêts et de plaisir, associées à des sentiments de culpabilité ou de dévalorisation de soi, un sommeil ou un appétit perturbé, des problèmes de concentration ou encore des idées suicidaires.</p>
              <p>Ces symptômes sont intenses, durables et entraînent un retentissement important sur les activités et le fonctionnement des individus, qui ont l'impression d'être littéralement submergés par des pensées négatives qu’ils ne peuvent contrôler.</p>
              <p>C’est pour quoi je propose, à travers mon application, un moyen  pour ces individus, d’organiser leurs pensées en les classant par catégories.
              De cette manière leurs pensées négatives sont matérialisées et identifiables. Une fois identifiées, ils en auront une meilleure visibilité et pourront ainsi plus facilement les bloquer et y remédier. </p>
          </div>

          <div class="design__strategie--container design__strategie--utilisateurs">
            <h4 class="projet">Étude utilisateurs</h4>

            <div class="design__utilisateurs--caracteristiques">
              <p>L’étude du groupe Baromètre santé 2017 nous présente les populations les plus touchées par la dépression, correspondant aux utilisateurs attendus de l’application web.</p>
                <br/>
              <div>
              <p class="caracteristiques">Caractéristiques socio-démographique des populations les plus touchées : </p>
              <img class="img__project no_top" src="../../assets/caracteristiques.png" alt="" />
              </div>


              <br/>
              <br/>
              <br/>
              

              <div>
              <p class="caracteristiques">Les populations les plus touchées : </p>
              <p>En 2017, parmi les personnes âgées de 18 à 75 ans, la prévalence de l’EDC dans l’année était estimée à 9,8% et était 2 fois plus élevée chez les femmes (13,0%) que chez les hommes (6,4%).</p>
              

              <p>La prévalence apparaissait la plus élevée entre 18 et 44 ans (environ 11,5%) puis diminuait de façon linéaire à partir de 45 ans. Chez les hommes, elle était maximale entre 18 et 34 ans et chez les femmes entre 35 et 44 ans (figure 1).</p>
              <!-- <img class="img__project homfam1" src="../../assets/homfam1.png" alt="" /> -->
              <img class="img__project homfam2" src="../../assets/homfam2.png" alt="" />
              

              <p>Selon le diplôme, toutes les catégories analysées sont concernées : les titulaires d’un diplôme inférieur au Bac présentaient en 2017 une augmentation de l’EDC de 1,9 points par rapport à 2005, ceux d’un Bac ou équivalent de 2,3 points et ceux d’un diplôme supérieur de 1,5 points. </p>
              <p>Selon la situation professionnelle, l’EDC a augmenté de plus de 5 points chez les chômeurs sur la période 2010-2017 et de 4 points chez les étudiants entre 2005 et 2017. </p>
              <p>Enfin, les personnes déclarant les plus faibles revenus restaient les plus exposées avec une augmentation de plus de 3 points entre 2010 et 2017.</p>
              <img class="img__project bactravail" src="../../assets/bactravail.png" alt="" />

